body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color:  #333;
}

.container {
    font-family: 'Rubik';
}
.main {
    background: #eee;
    color: #444;
    padding: 2em auto;
    width: 100%;
    min-height: 100vh;
    position: relative;
}
.header {
    text-align: center;
    padding: .8em 0;
    font-size: 1.7em;
    font-family: 'Source Sans Pro';
    letter-spacing: 1px;
    text-transform: uppercase;
}
.searchForm {
    padding: .8em 0;
    text-align: center;
}
.searchBox, .responseBox {padding: .8em 0;}
.searchBox input, .searchBox button {
    padding: 7px 15px;
    border: none;
    outline: none;
}
.searchBox input {
    border-radius: 50px 0 0 50px;
    color: #444;
    min-width: 40%;
}
.searchBox button {
    cursor: pointer;
    border-radius: 0 50px 50px 0;
    background-color: #fff;
    color: #999
}
.searchBox button:hover {
    color: #333;
}
.responseBox {
    font-size: .8em;
    color: #555;
}


.bookList {
    list-style: outside none none;
    margin: 0;
    padding: 30px 15px 60px;
    text-align: center;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

.bookCard {
    /* background-color: #fff; */
    margin: 20px;
    min-height: 400px;
    text-align: left;
    width: 200px;
    -webkit-transition: all 0.2s ease-in;
    transition: all 0.2s ease-in;
    /* border-radius: 5px; */
    position: relative;
}
.bookCard:hover {
    /* background-color: #fff; */
}
.bookImg {
    background-color: #ccc;
    display: block;
    margin: 0;
    width: 100%;
    height: 300px;
    border-radius: 5px;
}
.bookTitle {
    color: #444;
    padding: 5px 0;
    font-weight: 600;
}
.bookAuthor {
    color: #444;
    padding: 0;
    font-weight: 300;
    font-size: .9em;
}
.bookRating {
    position: absolute;
    left: 0;
    top: 265px;
    background: #d4d4d48f;
    padding: 8px;
    border-radius: 5px;
    color: #383838;
    font-weight: 800;
}
.bookOther {
    font-size: .8em;
    color: #555;
}

/* Style for pagination */
.pagination {
    bottom: 0;
    position: absolute;
    text-align: center;
    width: 100%;
}
.pagination span {
  color: black;
  padding: 8px 16px;
  text-decoration: none;
  -webkit-transition: background-color .3s;
  transition: background-color .3s;
}
.pagination span.active {
  background-color: dodgerblue;
  color: white;
}
.pagination span:hover:not(.active) {background-color: #ddd;}
